              <div class="right">
                <a @click="clickEvent(1)" href="javascript:void(0);"><i></i>点赞（100）</a>
                <a @click="clickEvent(1)" href="javascript:void(0);"><i></i>浏览（1000）</a>
                <a @click="clickEvent(1)" href="javascript:void(0);"><i></i>评论（398）</a>
              </div>
            </div>
          </div>
