            display: flex;
            align-items: center;

            .name {
              padding-left: 0.5rem;
              color: #606266;
            }

            .short-info {
              display: inline-block;
              margin-left: 0.5rem;
              text-overflow: ellipsis;
              white-space: nowrap;
              color: #606266;
              font-size: 0.9rem;
              width: 50ch;
              overflow: hidden;
            }
          }

          .right {
            a {
